/*  Language button */
.header {
  font-family: $rubik;
  background: transparentize(map-get($light, blueBayoux), 0.2);
  position: fixed;
  z-index: 10000;
  width: 100%;
  height: 4rem;
  left: 0;
  top: 0;
  display: flex;
  align-items: center;

  &__h1 {
    cursor: default;
    position: fixed;
    padding-left: 1rem;
  }

  nav {
    right: 1rem;
    position: absolute;

    ul {
      li {
        padding: 0rem 0.7rem;
        display: inline-block;
        font-weight: bold;
        font-size: 1rem;

        a {
          color: black;
          text-decoration: none;
          padding: 0 0.6rem 0 0.6rem;
          transition: color 0.3s ease;

          &:hover {
            color: map-get($light, LinkWater);
          }
        }
      }
    }
  }
}

//Darkmode
body {
  &.dark {
    .header {
      background: transparentize(map-get($dark, blue-bayoux-dark), 0.2);
      color: map-get($dark, mystic);

      a {
        color: map-get($dark, mystic);
      }
    }
  }
}
